import { Component, OnInit } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-edit-animation',
  templateUrl: './edit-animation.component.html',
  styleUrls: ['./edit-animation.component.css']
})
export class EditAnimationComponent implements OnInit {
  id: any;
  store: any;
  imageSrc:string = '';
  imageShow: any;
  editForm:FormGroup;


  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private formBuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {

    this.editForm=this.formBuilder.group({
      "editdata"    : ["",Validators.required],
     
    })
    this.activate.params.subscribe(params=>{
      this.id=params.id
      console.log(this.id)
    })
    this.editCategory()
  }

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e) {
    let reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc)
    this.imageShow=this.imageSrc
  }

  editCategory(){
    this.spinner.show()
    let object ={
        "categoryId" : this.id
    }
        this.service.postApi('admin/viewAnimationCategory',object,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
            if(res.body.response_code == 200){
              this.spinner.hide()
            this.store =res.body.result
            console.log('ress===>>',this.store)
          this.imageShow=this.store.image
          this.editForm.patchValue({
            "editdata" : this.store.categoryName
          })
        }
        
        },error=>{
          this.spinner.hide()
        })

    }
   
    
    editanimation(){
      this.spinner.show()
      let object ={
          "categoryId" : this.id,
          "categoryName" : this.editForm.value.editdata,
          "image" : this.imageShow
      }
          this.service.postApi('admin/editAnimationCategory',object,1).subscribe(res=>{
            if(res.body.response_code == 200){
              console.log("hhhhhhhhh=======>",res)
              this.spinner.hide()
              this.store =res.body.result
              console.log('ress===>>',this.store)
            this.imageShow=this.store.image
            this.route.navigate(['/animation-mnagement-category'])
            }
         
          },error=>{
            this.spinner.hide()
          })
          
  
      }
 



}
