import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit{
  loginForm:FormGroup;
  token: any;
  remembers: boolean=false;
  data: any;
  constructor(private service:DataService,private fb:FormBuilder,private router:Router,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.token=localStorage.removeItem('ok')
    if(!this.token){
      this.router.navigate(['dashboard'])
    }
    this.loginForm=this.fb.group({
      "email"        : ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])],
      "password"     : ["",Validators.compose([Validators.required,Validators.minLength(8),Validators.maxLength(16)])],
      "remember"      : [true,]
    })
     if(JSON.parse(localStorage.getItem('remembers'))==true){
      this.loginForm.patchValue({
        "email"     :  window.atob(localStorage.getItem('email')),
        "password"  :   window.atob(localStorage.getItem('password'))
      })
     }else{
       localStorage.clear();
      this.loginForm.reset();
     }
    
  }
  // ///////////LOG IN API ////////////

  login(){
    this.spinner.show()
    if(localStorage!=null){
      localStorage.setItem('email',window.btoa(this.loginForm.value.email))
      localStorage.setItem('password',window.btoa(this.loginForm.value.password))
      }else{
        this.loginForm.reset();
      }
    localStorage.setItem('remembers',JSON.stringify(this.loginForm.value.remember))
    let object={
      "email"     : this.loginForm.value.email,
      "password"  : this.loginForm.value.password
    }
    console.log('object',object)
    this.service.postApi('admin/login',object,0).subscribe((res)=>{
      console.log('res==>',res)
      if(res.body.response_code == 200){
        this.spinner.hide()
        this.service.showSuccess("You are login successfully")
        this.token=res.body.result.token
        this.data=res.body.result.permissions[0]
        localStorage.setItem('token',res.body.result.token)
        localStorage.setItem('permission',btoa(JSON.stringify(this.data)));
        this.router.navigate(['dashboard'])
      


      }else{
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
      }
    },error=>{
      this.spinner.hide()
    }) 
  
  }



  toggleShow() {
    this.show = !this.show;
    if (this.show) {
      this.type = "text";
    }
    else {
      this.type = "password";
    }
  }
type = "password";

  show = false;

}