import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  reserpasswordForm:FormGroup
  constructor(private service:DataService,private router:Router, private fb:FormBuilder) { }

  ngOnInit() {
    this.reserpasswordForm=this.fb.group({
      "email":["",Validators.compose([Validators.required,Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])]
    })
  }

  // /////////FORGOT PASSWORD API//////////
  forgotPassword(){
   let object={
    "url"    : this.service.websiteUrls+'reset-password',
    "email"  : this.reserpasswordForm.value.email
   }
   console.log('object',object)
   this.service.postApi('admin/forgotPassword',object,0).subscribe((res)=>{
   console.log('forgotPassword==res===>>',res)
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this.router.navigate(['login'])
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }

}
