import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user-view',
  templateUrl: './user-view.component.html',
  styleUrls: ['./user-view.component.css']
})
export class UserViewComponent implements OnInit {
  user_id: any;
  viewdata: any;

  constructor(private service:DataService,private router:Router,private activate:ActivatedRoute) { }

  ngOnInit() {
    // /////////////USER ID GET FROM USER MANAGEMENT COMPONENT/////////////
    this.activate.params.subscribe(res=>{
    this.user_id=res.id
    console.log('user_id===>>',this.user_id)
   })
   this.viewdetail()
  }

  // //////////////////////VIEW USER API///////////////////////

  viewdetail(){
    let object={
        "userId"  : this.user_id
    }
    this.service.postApi('admin/viewUser',object,1).subscribe(res=>{
    console.log('res==>>',res) 
    if(res.body.response_code == 200){
    this.viewdata=res.body.result
    console.log('viewdataasdas',this.viewdata)
    // this.service.showSuccess(res.body.response_message)     
    }else{
    this.service.toastErr(res.body.response_message)     
    }  
    })
    }
    }
