import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';



@Injectable({
  providedIn: 'root'
})
export class DataService {
  //  baseUrl = "http://172.16.6.217:1419/api/v1/"; 
  //  baseUrl = "http://ec2-35-176-66-190.eu-west-2.compute.amazonaws.com:1502/"
  // http://ec2-35-176-66-190.eu-west-2.compute.amazonaws.com:1502/
  // websiteUrls="http://ec2-35-176-66-190.eu-west-2.compute.amazonaws.com:1637/"
  // websiteUrls="http://172.16.6.14:1419/"
  websiteUrls = "http://172.16.6.170:4203/"
  // gfhasfhjashjasdshjas



   baseUrl = "http://ec2-35-176-66-190.eu-west-2.compute.amazonaws.com:1502/api/v1/"; 
  httpOptions: any;

  constructor(private http: HttpClient, private toastr: ToastrService, private spinner: NgxSpinnerService) { }

  typeLogin: string;
  postApi(url, data, isHeader): Observable<any> {
    if (isHeader == 1) {
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({
          "Content-Type": "application/json",
          // "JWT "
          "token": localStorage.getItem('token')
        }),
        observe: 'response'

      }
      // console.log("hjhjhjhjewrew",localStorage.getItem('token'));
    } else if (isHeader == 2) {
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({
          // "JWT "+
          "token": JSON.parse(localStorage.getItem('token'))
        }),
        observe: 'response'
      }

    }
    else {
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({
          "Content-Type": "application/json",
        }),
        observe: 'response'
      }

    }
    return this.http.post((this.baseUrl + url), data, httpOptions)

  }
  getApi(url, isHeader): Observable<any> {

    if (isHeader == 1) {
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({
          "Content-Type": "application/json",
          // "JWT "+
          "token": localStorage.getItem('token')

        }),
        observe: 'response'


      }
      // console.log("hjhjhjhj",JSON.parse(localStorage.getItem('token')));
    }
    else {
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({
          "Content-Type": "application/json",
        }),
        observe: 'response'
      }
    }
    return this.http.get((this.baseUrl + url), httpOptions)
  }
  showSuccess(msg, maxShown: any = '1') {
    this.toastr.success(msg);
  }

  toastErr(msg) {

    this.toastr.error(msg)
  }

  preventSpace(event) {
    if ((event.charCode == 32 || event.charCode == 64) && !event.target.value) {
      event.preventDefault();
    } else {
      // console.log(event)
    }
    // console.log('event charCode check', event.charCode)
  }

  spinners() {
    setTimeout(() => {
      /** spinner ends after 5 seconds */
      this.spinner.hide();

    }, 5000);
  }
}












































































































































































































































