import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-add-video',
  templateUrl: './add-video.component.html',
  styleUrls: ['./add-video.component.css']
})
export class AddVideoComponent implements OnInit {

  categortLists: any;
  addEpidodeForm:FormGroup;
  categoryId='';
  episodeId: any;
  videoSrc: any;
  safeSrc: any;
  episodeLists: any;


  constructor(private service:DataService,private activate:ActivatedRoute,private router:Router,private formBuilder:FormBuilder,private sanitizer: DomSanitizer,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.addEpidodeForm=this.formBuilder.group({
      "videoName" :  ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
    })
    this.episodeList()
    this.categoryList();

  }


  // /api/v1/admin/getAllAnimationEpisodes


  episodeList(){
    this.service.getApi('admin/getAllAnimationEpisodes',1).subscribe(res=>{
      console.log('resss==>>',res)
      this.episodeLists=res.body.result
      console.log('episodeLists==>>',this.episodeLists)
    })
  }


  categoryList(){
    this.service.getApi('admin/getAllAnimationCategories',1).subscribe(res=>{
      console.log('resss==>>',res)
      this.categortLists=res.body.result
      console.log('categoryLists==>>',this.categortLists)
    })
  }



  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /video/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.videoSrc = reader.result;
    // this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.videoSrc);
    // console.log('videoSrc',this.safeSrc)
  
}
 select(value){
  this.categoryId=value
  console.log('categoryName==>>',this.categoryId)
 }
 selects(value){
  this.episodeId=value
  console.log('episodeId==>>',this.episodeId)
 }
 

 addVideo(){
  // alert("jkdfhejkqwhfewfjklhewrfyer")
  this.spinner.show()
  let object={
    "categoryId"    : this.categoryId,
    "episodeId"     : this.episodeId,
    "videoName"     : this.addEpidodeForm.value.videoName,
    "video"         : this.videoSrc,
     
  }
  console.log('object',object)
  this.service.postApi('admin/addAnimationVideo',object,1).subscribe(res=>{
    console.log('resEdit===>>',res)

    if(res.body.response_code == 200 ){
      
      this.service.showSuccess(res.body.response_message)
      this.spinner.hide()
      this.videoSrc=res.body.result.docs
      // this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.videoSrc);
      this.router.navigate(['video-management'])
    }else{
      this.spinner.hide()
      this.service.toastErr("Please upload category video")
    }

  },error=>{
    this.spinner.hide()
    this.service.toastErr('Internal server error')
  })
  // this.router.navigate(['/animation-management-category'])
}

}
