import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-category',
  templateUrl: './view-category.component.html',
  styleUrls: ['./view-category.component.css']
})
export class ViewCategoryComponent implements OnInit {
  id: any;
  start: any;
 

  constructor(private service:DataService,private activate:ActivatedRoute) { }

  ngOnInit() {

    this.activate.params.subscribe(res=>{
      console.log('id===>>',res.id)
      this.id=res.id
    })
    this.viewAnimationCategory()
  }
  viewAnimationCategory(){
    let object ={
        "categoryId" : this.id
    }
        this.service.postApi('admin/viewAnimationCategory',object,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
            this.start =res.body.result
            console.log('ress===>>',this.start)

        })

    }

}
