import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-sub-admin',
  templateUrl: './edit-sub-admin.component.html',
  styleUrls: ['./edit-sub-admin.component.css']
})
export class EditSubAdminComponent implements OnInit {
  subAdminForm:FormGroup;
  SubAdmin_id: any;
  subadmindata: any;
  constructor(private service:DataService,private  spinner:NgxSpinnerService,private router:Router,private formBuilder:FormBuilder,private activate:ActivatedRoute) { }

  ngOnInit() {
   
    this.subAdminForm=this.formBuilder.group({
      "name"                          :   ['',Validators.compose([Validators.required, Validators.maxLength(60),Validators.minLength(2),Validators.pattern("[a-zA-Z ]*")])],
      "email"                         :   ["",Validators.compose([Validators.required, Validators.maxLength(60),Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,3}))$/)])],
      "number"                        :   ["",Validators.compose([Validators.required, Validators.maxLength(16),Validators.pattern(/^[1-9][0-9]{9}$/)])],
      "password"                      :   ["",[Validators.required,Validators.minLength(8),Validators.maxLength(16),Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/i)]],
      "ConfirmPasssword"              :   ["",Validators.required],
      "dashboard"                     :   [false],
      "userManagement"                :   [false],
      "subAdminManagement"            :   [false],
      "animationManagement"           :   [false],
      "podcastManagement"             :   [false],
      "mentalHealthCampaign"          :   [false],
      "medicalConditionKnowledge"     :   [false],
      "contactUsManagement"           :   [false],
      "subscriptionManagement"        :   [false],
      "transactionManagement"         :   [false],
      "staticContentManagement"       :   [false],
    })
    // this.listdata()
    this.activate.params.subscribe(params=>{
      this.SubAdmin_id=params.id
      console.log('id===',this.SubAdmin_id)
    })
    this.viewData()
  }


  viewData(){
    this.service.getApi('admin/viewSubAdmin/'+this.SubAdmin_id,1).subscribe(res=>{
      if(res.body.response_code == 200 ){
        this.subadmindata=res.body.result

        console.log('subadmindata_id',this.subadmindata.permissions[0]._id)
        this.subAdminForm.patchValue({
          "name"                          :   this.subadmindata.firstName,
          "email"                         :   this.subadmindata.email,
          "number"                        :   this.subadmindata.mobileNumber,
          // "password"                      :   window.btoa(this.subadmindata.password),
          "userManagement"                :   this.subadmindata.permissions[0].userManagement, 
          "subAdminManagement"            :   this.subadmindata.permissions[0].subAdminManagement, 
          "animationManagement"           :   this.subadmindata.permissions[0].animationManagement, 
          "podcastManagement"             :   this.subadmindata.permissions[0].podcastManagement,
          "mentalHealthCampaign"          :   this.subadmindata.permissions[0].mentalHealthCampaign,  
          "medicalConditionKnowledge"     :   this.subadmindata.permissions[0].medicalConditionKnowledge, 
          "contactUsManagement"           :   this.subadmindata.permissions[0].contactUsManagement, 
          "subscriptionManagement"        :   this.subadmindata.permissions[0].subscriptionManagement, 
          "transactionManagement"         :   this.subadmindata.permissions[0].transactionManagement, 
          "staticContentManagement"       :   this.subadmindata.permissions[0].staticContentManagement, 
        })
        console.log('patchVale==>>>',this.subAdminForm.patchValue)
      }
    })
  }


editSubmit(){
  this.spinner.show()
  let object={
    "subAdminId"                    :   this.SubAdmin_id,
    "permissionId"                  :   this.subadmindata.permissions[0]._id,
    "name"                          :   this.subAdminForm.value.name,
    "email"                         :   this.subAdminForm.value.email,
    "mobileNumber"                  :   this.subAdminForm.value.number,
    "password"                      :   this.subAdminForm.value.password,
    "ConfirmPasssword"              :   this.subAdminForm.value.ConfirmPasssword,
    "dashboard"                     :   this.subAdminForm.value.dashboard, 
    "userManagement"                :   this.subAdminForm.value.userManagement, 
    "subAdminManagement"            :   this.subAdminForm.value.subAdminManagement, 
    "animationManagement"           :   this.subAdminForm.value.animationManagement, 
    "podcastManagement"             :   this.subAdminForm.value.podcastManagement,
    "mentalHealthCampaign"          :   this.subAdminForm.value.mentalHealthCampaign,  
    "medicalConditionKnowledge"     :   this.subAdminForm.value.medicalConditionKnowledge, 
    "contactUsManagement"           :   this.subAdminForm.value.contactUsManagement, 
    "subscriptionManagement"        :   this.subAdminForm.value.subscriptionManagement, 
    "transactionManagement"         :   this.subAdminForm.value.transactionManagement, 
    "staticContentManagement"       :   this.subAdminForm.value.staticContentManagement, 
  }
  console.log('object===>>>',object)
  this.service.postApi('admin/editSubAdmin',object,1).subscribe(res=>{
    console.log('res=====>>>>',res)
    if(res.body.response_code == 200 ){
      this.service.showSuccess(res.body.response_message)
      this.spinner.hide()
      this.router.navigate(['sub-admin-management'])
    } else if(res.body.response_code == 409){
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
    }else{
      this.spinner.hide()
      this.service.toastErr(res.body.response_message)
    }
    
  },error=>{
    this.spinner.hide()
    this.service.toastErr('Internal server error')
  })
}





toggleShow() {
  this.show = !this.show;
  if (this.show) {
    this.type = "text";
  }
  else {
    this.type = "password";
  }
}
type = "password";

show = false;
toggleShows() {
  this.show1 = !this.show1;
  if (this.show1) {
    this.type1 = "text";
  }
  else {
    this.type1 = "password";
  }
}
type1 = "password";

show1 = false;
}



