import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../data.service';
declare var $:any;
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  datapermission: any;

  constructor(private router:Router,private service:DataService) { }

  ngOnInit() {
    this.datapermission=JSON.parse(window.atob(localStorage.getItem('permission')))
    console.log('datapermission==>>',this.datapermission.dashboard)
  }

  
  // outMOdal(){
  //   $('#modallogout').modal({ backdrop: 'static', keyboard: false })
  // }
  logout(){
    // $("#modallogout").modal("hide");
    $('#modallogout').modal({ backdrop: 'static', keyboard: false })
    localStorage.removeItem('token')
    this.service.showSuccess('Logout successfully')
    
    setTimeout(
      function(){ 
        window.location.reload();
      }, 100);
    this.router.navigate(['login'])
    
  }
  routers(){
    this.router.navigate(['episode-management'])
  }
}
