import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-categorymental-health',
  templateUrl: './add-categorymental-health.component.html',
  styleUrls: ['./add-categorymental-health.component.css']
})
export class AddCategorymentalHealthComponent implements OnInit {

  imageUrl: string;
  tokenId: any;
  type: any;
  base64Image: any;
  private imageSrc: string = '';
 enteredName: '';
  CategoryName: any;
  mentalhealthForm: FormGroup;
  base64textString: string;
 


  constructor(private Service:DataService,private router:Router,private fb:FormBuilder) { }

  ngOnInit() {
    this.mentalhealthForm=this.fb.group({
      names      : ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
    })
   // this.tokenId = JSON.parse(localStorage.getItem('token'))
       // this.imageUrl = "assets/pic_trulli.jpg";
       // this.addAnimationCategory()
  }

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc)
  }

  addHealthCategory(){
    // alert("jkdfhejkqwhfewfjklhewrfyer")
    let object={
      "categoryName"   : this.mentalhealthForm.value.names,
      "image"  : this.imageSrc,
       
    }
    console.log('object',object)
    this.Service.postApi('admin/addMentalHealthCategory',object,1).subscribe(res=>{
      console.log('resEdit===>>',res)
      // if(res.body.response_code == 200 ){
      // this.CategoryName=res.body.result.profilePic
      // console.log('profile===>>',this.CategoryName)
      // // this.myProfile();
      // }

    })
    this.router.navigate(['/mental-health-category'])
  }

 


}
