import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  resetPasswordForm:FormGroup;
  id: any;
  show2: boolean=false;
  show1: boolean = false;
  type :any= "password";

  show :boolean = false;
  type2: string="password";
  type1: string="password";

  constructor(private service:DataService,private router:Router,private activeRouter:ActivatedRoute,private fb:FormBuilder) { }
  // firstName         : ['',Validators.compose([Validators.required,Validators.maxLength(56),Validators.pattern(/^(?=.{1,50}$)[a-z]+(?:['_.\s][a-z]+)*$/i)])],
  ngOnInit() {
    this.activeRouter.params.subscribe(res=>{
     
      this.id=res.result
      console.log('id=>',this.id)
    })
    this.resetPasswordForm=this.fb.group({
      'newPassword'     :   ['',Validators.compose([Validators.required,Validators.minLength(8),Validators.maxLength(16)])],
      'confirmPassword' :   ['',Validators.required]
    })
  }

  //////////////RESET PASSWORD////////////////////


  reserPassword(){
    let object={
      // "_id"               : this.id,
      "newPassword"       : this.resetPasswordForm.value.newPassword,
      "confirmPassword"   : this.resetPasswordForm.value.confirmPassword 

    }
    // console.log('iddddd',this.id)
    // console.log('object',object)
    this.service.postApi('admin/resetPassword/'+this.id,object,0).subscribe(res=>{
      console.log('reset===>>',res)
      if(res.status == 200){
        this.service.showSuccess(res.body.response_message)
        this.router.navigate(['login'])
      }else{
        this.service.toastErr(res.body.response_message)
      }
    

    },error=>{
      this.service.toastErr("Internal server error");
    })
  }
  toggleShow1() {
    
    this.show = !this.show;
    if (this.show) {
      this.type = "text";
    }
    else {
      this.type = "password";
    }
    }
    toggleShow2() {
      this.show2 = !this.show2;
      if (this.show2) {
        this.type2 = "text";
      }
      else {
        this.type2 = "password";
      }
    }



  

}

