import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../data.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-episod-edit',
  templateUrl: './episod-edit.component.html',
  styleUrls: ['./episod-edit.component.css']
})
export class EpisodEditComponent implements OnInit {
  id: any;
  store: any;
  imageSrc:string = '';
  imageShow: any;
  editForm:FormGroup;
  categortLists: any;
  selectValue: any;
  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private formBuilder:FormBuilder) { }

  ngOnInit() {
    this.editForm=this.formBuilder.group({
      "episodeName"    : ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])],
     
    })
    this.activate.params.subscribe(params=>{
      this.id=params.id
      console.log('id===',this.id)
    })
    this.viewAnimationCategory()
    this.categoryList()
  }

 



  viewAnimationCategory(){
    // let object ={
    //     "episodeId" : this.id
    // }
    // console.log('episodeId==>>',object)
        this.service.getApi('admin/viewAnimationEpisode/'+this.id,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
            this.store =res.body.result
            
          this.imageShow=this.store.image
          console.log('imageShow===>>',this.store.status)
          this.editForm.patchValue({
            "episodeName"   : this.store.categoryName
          })

        //  this.route.navigate(['/animation-management-category'])
          // [routerLink]="['/animation-management-category']"
        })

    }

    categoryList(){
      // let object ={
      //   "status"    : null
      // }
      // console.log('status==>>',object)
      this.service.getApi('admin/getAllAnimationCategories',1).subscribe(res=>{
        console.log('resss==>>',res)
        this.categortLists=res.body.result
        console.log('categoryLists==>>',this.categortLists)
      })
    }

    handleInputChange(e) {
      var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
      var pattern = /image-*/;
      var reader = new FileReader();
      if (!file.type.match(pattern)) {
        alert('invalid format');
        return;
      }
      reader.onload = this._handleReaderLoaded.bind(this);
      reader.readAsDataURL(file);
    }
  
    _handleReaderLoaded(e) {
      let reader = e.target;
      this.imageSrc = reader.result;
      console.log(this.imageSrc)
      this.imageShow=this.imageSrc
    }

    select(value){
      this.selectValue=value
      console.log('selectValue',this.selectValue)
    }

    editAnimationCategory(){
      let object ={
          "episodeId" : this.id,
          "episodeName" : this.editForm.value.episodeName,
          "categoryId" : this.selectValue,
          "image"       :this.imageShow,
      }
      console.log('update==>>',object)
          this.service.postApi('admin/editAnimationEpisode',object,1).subscribe(res=>{
              console.log("hhhhhhhhh=======>",res)
              this.store =res.body.result
              console.log('ress===>>',this.store)
            this.imageShow=this.store.image
            this.route.navigate(['/episode-management'])
            // [routerLink]="['/animation-management-category']"
          })
  
      }
}
