import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-edit-audio',
  templateUrl: './edit-audio.component.html',
  styleUrls: ['./edit-audio.component.css']
})
export class EditAudioComponent implements OnInit {
  id: any;
  store: any;
  imageShow: any;
  videos: any;
  yt_iframe_html: any;
  safeSrc: any;
  addaudioForm:FormGroup;
  listForm:FormGroup;
  videoSrc: any;
  episodeLists: any;
  categortLists: any;
  images: any;
  audioUrl: any;
  episod: any;
  category: any;


  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private sanitizer: DomSanitizer,private formBuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    // this.addaudioForm=this.formBuilder.group({
    //   audioName       : [""],
    // })
    this.addaudioForm=this.formBuilder.group({
      "audioName" :  ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
      })

    this.listForm= this.formBuilder.group({
      "episodeName"     :["",],
      "categoryNames"   :["",]
    })
    this.activate.params.subscribe(params=>{
      this.id=params.id
      console.log('id===',this.id)
    })
    this.viewVedeo()
    this.episodeList()
    this.categoryList()
  }


  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /audio/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }

     
      
    
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
    reader.onload = (_event) => { 
      this.audioUrl = reader.result; 
    }
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.videoSrc = reader.result;
    // this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.videoSrc);
    // console.log('videoSrc',this.safeSrc)
  
}

episodeList(){
  this.service.getApi('admin/getAllPodcastEpisodes',1).subscribe(res=>{
    console.log('resss==>>',res)
    this.episodeLists=res.body.result
    console.log('episodeLists==>>',this.episodeLists)
  })
}


categoryList(){
  this.service.getApi('admin/getAllPodcastCategories',1).subscribe(res=>{
    console.log('resss==>>',res)
    this.categortLists=res.body.result
    console.log('categoryLists==>>',this.categortLists)
  })
}

selects(){
  // console.log('value1',this.listForm.value.episodeName)
  this.episod=this.listForm.value.episodeName
  console.log('value1',this.episod)
}
select(){
  // console.log('value2',this.listForm.value.categoryNames)
  this.category=this.listForm.value.categoryNames
}
  viewVedeo(){
        this.service.getApi('admin/viewPodcastAudio/'+this.id,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
          if(res.body.response_code == 200){
            this.store =res.body.result
            console.log('store===>>',this.store)
            // this.audioUrl =  this.sanitizer.bypassSecurityTrustResourceUrl(this.store.video);
            this.audioUrl =  this.store.audio
             console.log('episodeName===>>',this.audioUrl)
             this.addaudioForm.patchValue({
               "audioName"   :  this.store.audioName,
              
             })



    this.listForm.patchValue({

      "episodeName" : this.store.episodeId,
      "categoryNames":this.store.categoryId
     })


   
        
   console.log('PatchValue', this.listForm.value)
  
       
            }

           
     
        },error=>{
          this.service.toastErr('Internal server error')
        })

    }

    editvideo(){
      this.spinner.show()
      let object={
        "audioId"       : this.id,
        "audioName"     : this.addaudioForm.value.audioName,
        "audio"         : this.audioUrl,
        "categoryId"    : this.category,
        "episodeId"     : this.episod
      }
      console.log('videoEdit',object)
      this.service.postApi('admin/editPodcastAudio',object,1).subscribe(res=>{
        if(res.body.response_code == 200){

        this.service.showSuccess(res.body.response_message)
        this.spinner.hide()
        this.route.navigate(['audio-management'])
        }else{
          this.spinner.hide()
          this.service.toastErr(res.body.response_message)
        }
      },error=>{
        this.service.toastErr('Internal server error')
      })

}

}



