import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $: any;
@Component({
        selector: 'app-user-management',
        templateUrl: './user-management.component.html',
        styleUrls: ['./user-management.component.css']
})
export class UserManagementComponent implements OnInit {
        searchname: any;
        searchForm: FormGroup;
        token: any;
        tokens: string;
        userdata: any;
        limit = 10;
        total: any;
        page = 1;
        p = 0;
        user_id: any;
        block_id: any;
        unblock_id: any;
        unblock_ids: any;
        search: string;

        constructor(private service: DataService, private router: Router, private formBuilder: FormBuilder, private spinner: NgxSpinnerService) {
                this.lists();
        }

        ngOnInit() {

        }


        Searchvaluechange(searchdata) {
                this.searchname = searchdata
                this.page=1
                console.log('searchName==>>', this.searchname)
                this.lists()
        }

        reset() {
                this.searchname = ''
                this.search = '';
                this.lists()


        }


        lists() {  
                this.spinner.show()
                this.userdata = [];
                let object = {
                        "search": this.searchname,
                        "page": this.page,
                        "limit": this.limit,
                }
                console.log('page===>>', object)
                this.service.postApi('admin/userList', object, 1).subscribe(res => {
                        console.log('resdfdsfsdfdss==>>', res)
                        if (res.body.response_code == 200) {
                                this.spinner.hide()
                                this.userdata = res.body.result.docs ? res.body.result.docs : res.body.result
                                console.log('userdata===>', this.userdata)
                                this.total = res.body.result.total
                                console.log('userdata==>>', this.total, this.userdata)

                        } else {
                                this.spinner.hide()
                        }
                }, error => {
                        this.spinner.hide()
                })
        }

        changePage(page) {
                console.log('Page ', page)
                this.page = page;
                this.p = page - 1;
                this.lists()
        }

    

        deleteFunction(id) { 

                this.user_id = id
                $('#modaldelete').modal({ backdrop: 'static', keyboard: false })
        }

        deleteFunctions() {  


                let object = {
                        "userId": this.user_id
                }
                this.service.postApi('admin/deleteUser', object, 1).subscribe(res => {
                        console.log('res===>>>', res)
                        if (res.body.response_code == 200) {
                                this.service.showSuccess(res.body.response_message)
                                this.lists()
                        } else {
                                this.service.toastErr(res.body.response_message)
                        }
                })
        }




        block(_id) { 


                this.block_id = _id
                console.log('block_id', this.block_id)
                $('#modalblock').modal({ backdrop: 'static', keyboard: false })
        }

        blockFunction() { 


                let object = {
                        "userId": this.block_id
                }
                this.service.postApi('admin/blockUnblockUser', object, 1).subscribe(res => {
                        console.log('res_block')
                        if (res.body.response_code == 200) {
                                this.service.showSuccess(res.body.response_message)
                                this.lists()

                        } else {
                                this.service.toastErr(res.body.response_message)
                        }
                })
        }


        unblock(id) { 


                this.unblock_id = id
                console.log('unblock_id', this.unblock_id)
                $('#modalunblock').modal({ backdrop: 'static', keyboard: false })
        }

        unblockFuntion() { 


                let object = {
                        "userId": this.unblock_id
                }
                this.service.postApi('admin/blockUnblockUser', object, 1).subscribe(res => {
                        console.log('unblock_res', res)
                        if (res.body.response_code == 200) {
                                this.service.showSuccess(res.body.response_message)
                                this.lists()
                        } else {
                                this.service.toastErr(res.body.response_message)
                        }

                })
        }

}

