import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-sub-admin-management',
  templateUrl: './sub-admin-management.component.html',
  styleUrls: ['./sub-admin-management.component.css']
})
export class SubAdminManagementComponent implements OnInit {
  limit = 10;
  total: any;
  page = 1;
  p = 0;
  subAdmindata: any=[];
  searchname: any;
  videoDeleteId: any;
  search: string;
  unblock_id: any;
  block_id: any;



  constructor(private service: DataService, private router: Router,private spinner:NgxSpinnerService) {
    this.postcastlists()
   }

  ngOnInit() {
   
   
  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1;
    this.postcastlists()
    }

  reset() {
    this.searchname = ''
    this.search='';
    this.postcastlists()


  }

  postcastlists() {
    this.spinner.show()
    this.subAdmindata=[];
    let object = {
      "search": this.searchname,
      "page": this.page,
      "limit": this.limit,
    }
      this.service.postApi('admin/subAdminList', object, 1).subscribe(res => {
      if(res.body.response_code == 200 ){
      this.spinner.hide()
      this.subAdmindata = res.body.result.docs ? res.body.result.docs : res.body.result
      this.total = res.body.result.total
      }else{
        this.spinner.hide()
      }


    },error=>{
      this.spinner.hide()
      this.service.toastErr('Internal server error')
    })
  }

  changePage(page) {
    console.log('Page ', page)
    this.page = page;
    this.p = page - 1;
    this.postcastlists()
  }

  deleteId(id){
    this.videoDeleteId=id
    $('#modalDelete').modal({ backdrop: 'static', keyboard: false })
  }

  deleteFunctions(){ 

    let object={
    "subAdminId" : this.videoDeleteId
    }
    this.service.postApi('admin/deleteSubAdmin',object,1).subscribe(res=>{
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this.postcastlists()
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }

block(_id){ 
  this.block_id=_id
  $('#modalblock').modal({ backdrop: 'static', keyboard: false })
  }
blockFunction(){ 
  let object={
  "subAdminId"  : this.block_id
  }
  this.service.postApi('admin/blockUnblockSubAdmin',object,1).subscribe(res=>{
  if(res.body.response_code == 200){
  this.service.showSuccess(res.body.response_message)
   this.postcastlists()

  }else{
  this.service.toastErr(res.body.response_message)
  }
  })
  }
unblock(id){ 
  this.unblock_id=id
  $('#modalunblock').modal({ backdrop: 'static', keyboard: false })
  }

unblockFuntion(){ 


  let object={
  "subAdminId"  :  this.unblock_id
  }
  this.service.postApi('admin/blockUnblockSubAdmin',object,1).subscribe(res=>{
  if(res.body.response_code == 200){
  this.service.showSuccess(res.body.response_message)
   this.postcastlists()
  }else{
  this.service.toastErr(res.body.response_message)
  }

  })
  }

}
