import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-mental-health-campaign-category',
  templateUrl: './mental-health-campaign-category.component.html',
  styleUrls: ['./mental-health-campaign-category.component.css']
})
export class MentalHealthCampaignCategoryComponent implements OnInit {
  limit = 10;
  total: any;
  page = 1;
  p = 0;
  helthdata: any=[];
  searchname: any;
  helthDeleteId: any;
  search: string;
  unblock_id: any;
  block_id: any;

 


  constructor(private service: DataService, private router: Router,private spinner:NgxSpinnerService) {
    this. categorylists()
   }

  ngOnInit() {
   
   
  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1
    console.log('searchName==>>', this.searchname)
    this. categorylists()
    }

  reset() {
    this.searchname = ''
    this.search='';
    this. categorylists()


  }

  categorylists() {
    this.spinner.show()
    this.helthdata=[];
    let object = {
      "search": this.searchname,
      "page": this.page,
      "limit": this.limit,
    }
      this.service.postApi('admin/mentalHealthCategoryList', object, 1).subscribe(res => {
      if(res.body.response_code == 200 ){
      this.spinner.hide()
      this.helthdata = res.body.result.docs ? res.body.result.docs : res.body.result
      this.total = res.body.result.total
      }else{
        this.spinner.hide()
      }


    },error=>{
      this.spinner.hide()
      this.service.toastErr('Internal server error')
    })
  }

  changePage(page) {
    console.log('Page ', page)
    this.page = page;
    this.p = page - 1;
    this. categorylists()
  }

  deleteId(id){
    this.helthDeleteId=id
    $('#modalDelete').modal({ backdrop: 'static', keyboard: false })
    console.log('helthDeleteId',this.helthDeleteId)
  }

  deleteFunctions(){ 

    let object={
    "categoryId" : this.helthDeleteId
    }
    this.service.postApi('admin/deleteMentalHealthCategory',object,1).subscribe(res=>{
    console.log('res===>>>',res)
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this. categorylists()
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }

block(_id){ 
  this.block_id=_id
  console.log('block_id',this.block_id)
  $('#modalblock').modal({ backdrop: 'static', keyboard: false })
  }
blockFunction(){ 
  let object={
  "categoryId"  : this.block_id
  }
  this.service.postApi('admin/blockUnblockMentalHealthCategory',object,1).subscribe(res=>{
  console.log('res_block',)
  if(res.body.response_code == 200){
  this.service.showSuccess(res.body.response_message)
   this. categorylists()

  }else{
  this.service.toastErr(res.body.response_message)
  }
  })
  }
unblock(id){ 
  this.unblock_id=id
  console.log('unblock_id',this.unblock_id)
  $('#modalunblock').modal({ backdrop: 'static', keyboard: false })
  }

unblockFuntion(){ 


  let object={
  "categoryId"  :  this.unblock_id
  }
  this.service.postApi('admin/blockUnblockMentalHealthCategory',object,1).subscribe(res=>{
  console.log('unblock_res',res)
  if(res.body.response_code == 200){
  this.service.showSuccess(res.body.response_message)
   this. categorylists()
  }else{
  this.service.toastErr(res.body.response_message)
  }

  })
  }


}
