import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-contact-us-management',
  templateUrl: './contact-us-management.component.html',
  styleUrls: ['./contact-us-management.component.css']
})
export class ContactUsManagementComponent implements OnInit {
  limit=10;
  total: any;
  page=1;
  p = 0;
  contactdata: any;

  userId: any;
  searchname: any;
  searchs: any;
  constructor(private service:DataService,private router:Router,private formBuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
   
    this.contactlists()
  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    console.log('searchName==>>', this.searchname)
    this.contactlists()
    }

  contactlists(){  
    this.spinner.show()
    this.contactdata=[];
    let object={
    "search" : this.searchname,
    "page": this.page,
    "limit": this.limit,
    }
    console.log('page===>>',object)
    this.service.postApi('admin/contactUsList',object,1).subscribe(res=>{
    console.log('resdfdsfsdfdss==>>',res)
    if(res.body.response_code == 200 ){
    this.spinner.hide()
    this.contactdata=res.body.result.docs?res.body.result.docs:res.body.result
    console.log('userdata===>',this.contactdata)
    // this.limit=res.body.result.docs.limit
    this.total=res.body.result.total
    console.log('userdata==>>',this.total, this.contactdata)
    }else{
      this.spinner.hide()
    }
  
    },error=>{
      this.service.toastErr('Internal serve error')
    })
    }
    changePage(page) {
      console.log('Page ', page)
      this.page = page;
      this.p = page-1;
      this.contactlists()   
      }
      reset(){
        this.searchname='';
        this.searchs='';

        this.contactlists()
       
        
}

    deleteFunction(id){
      this.userId=id
    }
    deleteuser(){
      let object={
        "contactUsId"  : this.userId
      }
      this.service.postApi('admin/deleteContactUs',object,1).subscribe(res=>{
       if(res.body.response_code == 200){
         this.service.showSuccess(res.body.response_message)
         this.contactlists()
       }else{
        this.service.showSuccess(res.body.response_message)
       }
      },error=>{
        this.service.toastErr('Internal server error')
      })
    }
    
}
