import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-static-view',
  templateUrl: './static-view.component.html',
  styleUrls: ['./static-view.component.css']
})
export class StaticViewComponent implements OnInit {
  viewdata: any;
  static_type: any;

  constructor(private service:DataService, private router:Router,private activate:ActivatedRoute) { }

  ngOnInit() {
    // ///////////THIS ID GET FROM STATIC PAGE MANAGEMENT /////////
    this.activate.params.subscribe(res=>{
      console.log('staic_id',res.type)
      this.static_type=res.type
      console.log('staic_id',this.static_type)
    })
    // ///////////THIS METHOD OF STATIC VIEW DATA////////////
    this.staticvied();
  }

  // ///////////////////STATIC VIEW API ///////////////
 staticvied(){
 let object={
   "type"  : this.static_type
 }
 this.service.postApi('static/viewStaticContent',object,0).subscribe(res=>{
  console.log('staticView==>>',res)
  if(res.body.response_code ==200){
    this.viewdata=res.body.result
    console.log('viewdata',this.viewdata)
  }
 })
 }
}
