import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-subscription-management',
  templateUrl: './subscription-management.component.html',
  styleUrls: ['./subscription-management.component.css']
})
export class SubscriptionManagementComponent implements OnInit {


  limit = 10;
  total: any;
  page = 1;
  p = 0;
  podcastdata: any=[];
  searchname: any;
  subscriDeleteId: any;
  search: string;
  unblock_id: any;
  block_id: any;
  subscriptionList: any;



  constructor(private service: DataService, private router: Router,private spinner:NgxSpinnerService) {
    this.getSubscriptionList()
   }

  ngOnInit() {
   this.getSubscriptionList();
   
  }

  reset() {
    this.searchname = ''
    this.search='';
    this.getSubscriptionList()


  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1
    console.log('searchName==>>', this.searchname)
    this.getSubscriptionList()
    }
// -------------------------------subscription-management list------------------------------------//
  getSubscriptionList(){
    this.subscriptionList=[];
    this.spinner.show()
    let object = {
      "search": this.searchname,
      "page": this.page,
      "limit": this.limit,
    }
    this.service.postApi('admin/subscriptionList',object,1).subscribe(res=>{
      if(res.body.response_code == 200 ){
        this.subscriptionList=res.body.result.docs ? res.body.result.docs : res.body.result
          this.spinner.hide()
      }else{
        this.spinner.hide()
      }
    },error=>{
      this.spinner.hide()
        this.service.toastErr('Internal server error')
    })
  }

  changePage(page) {
    console.log('Page ', page)
    this.page = page;
    this.p = page - 1;
    this.getSubscriptionList()
  }

  deleteId(id){
    this.subscriDeleteId=id
    $('#modalDelete').modal({ backdrop: 'static', keyboard: false })
    console.log('subscriDeleteId',this.subscriDeleteId)
  }

 

deletersusbscrption(){
  let object={
    "subscriptionId" : this.subscriDeleteId
    }

  this.service.postApi('admin/deleteSubscription',object,1).subscribe(res=>{
    console.log('deleteSubscription,res====>>>',res)
    if(res.body.response_code == 200 ){

      this.service.showSuccess(res.body.response_message)
      this.getSubscriptionList()
    }
  },error=>{
    this.service.toastErr('Internal server error')
  })
}


}
