import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-addepisode-podcast',
  templateUrl: './addepisode-podcast.component.html',
  styleUrls: ['./addepisode-podcast.component.css']
})
export class AddepisodePodcastComponent implements OnInit {
  categortLists: any;
  addEpidodeForm:FormGroup;
  imageSrc: any;
  categoryId='';
    object: { "categoryId": string; "episodeName": any; "image": any; };


  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private formBuilder:FormBuilder) { }

      ngOnInit() {
      
      this.addEpidodeForm=this.formBuilder.group({
      "episodeNames" :  ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
      })
      this.categoryList();
      }
      // /////////////Admin Animation Category List ///////////////
      categoryList(){
      // let object ={
      // "status"    : null
      // } 
      // console.log('status==>>',object)
      this.service.getApi('admin/getAllPodcastCategories',1).subscribe(res=>{
      console.log('resss==>>',res)
      this.categortLists=res.body.result
      console.log('categoryLists==>>',this.categortLists)
      })
      }

      // ////////Event for image upload 64base ///////////////

  handleInputChange(e) {
      var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
      var pattern = /image-*/;
      var reader = new FileReader();
      if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
      }
      reader.onload = this._handleReaderLoaded.bind(this);
      reader.readAsDataURL(file);
      }
  _handleReaderLoaded(e) {
      let reader = e.target;
      this.imageSrc = reader.result;
  
      }
      //////////Evenet for fet category id//////////////////////
  select(value){
      this.categoryId=value
      console.log('categoryName==>>',this.categoryId)
      }

      // //////////Add episode amination api////////////////////
 addEpisode(){
  
   let object={
    "categoryId"    : this.categoryId,
    "episodeName"   : this.addEpidodeForm.value.episodeNames,
    "image"         : this.imageSrc,
     
    }
    console.log('object',object)
    this.service.postApi('admin/addPodcastEpisode',object,1).subscribe(res=>{
    console.log('resEdit===>>',res)

    if(res.body.response_code == 200 ){
    this.service.showSuccess(res.body.response_message)
    this.route.navigate(['episode-management-podcast'])
    }else{
    this.service.toastErr('Please upload episode image ')
    }

    },error=>{
    this.service.toastErr('Internal server error')
    })

  

    }

}
