import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';

import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-view-video',
  templateUrl: './view-video.component.html',
  styleUrls: ['./view-video.component.css']
})
export class ViewVideoComponent implements OnInit {

  id: any;
  store: any;
  imageShow: any;
  videos: any;
  yt_iframe_html: any;
  safeSrc: any;

  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.activate.params.subscribe(params=>{
      this.id=params.id
      console.log('id===',this.id)
    })
    this.viewAnimationCategory()
  }
  viewAnimationCategory(){
    // let object ={
    //     "episodeId" : this.id
    // }
    // console.log('episodeId==>>',object)
        this.service.getApi('admin/viewAnimationVideo/'+this.id,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
            this.store =res.body.result
            
          this.imageShow=this.store.image
          this.videos=this.store.video
          console.log('videos===>>',this.videos)
          this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.videos);

        
        })

    }

}
