import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.css']
})
export class AddCategoryComponent implements OnInit {
  imageUrl: string;
  tokenId: any;
  type: any;
  base64Image: any;
  private imageSrc: string = '';
  enteredName: '';
  CategoryName: any;
  myAnimationForm: FormGroup;
  base64textString: string;
  object: { "categoryName": any; "image": string; };

  constructor(private Service:DataService,private router:Router,private fb:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
      this.myAnimationForm=this.fb.group({
      names      : ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
      })

      }

  // ////////Event for image upload  in 64 base//////////////////////
  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc)
  }

  
    
  addAnimationCategory(){
   
    if(this.imageSrc!=""){
        this.spinner.show()
        this.spinner.show()
        this.object={
          "categoryName": this.myAnimationForm.value.names,
          "image"  : this.imageSrc,
           
        }

    
    this.Service.postApi('admin/addAnimationCategory',this.object,1).subscribe(res=>{
      console.log('resEdit===>>',res)
      if(res.body.response_code == 200 ){
        // setTimeout(() => {
          this.spinner.hide()
          
          this.router.navigate(['/animation-mnagement-category'])
        
      }
    })
  }else{
    this.Service.toastErr('Please upload category image')
  }
    // console.log('object',object)
    
  }

 

 

}
