import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-edit-medical-condition-knowledge',
  templateUrl: './edit-medical-condition-knowledge.component.html',
  styleUrls: ['./edit-medical-condition-knowledge.component.css']
})
export class EditMedicalConditionKnowledgeComponent implements OnInit {

  id: any;
  store: any;
  editForm: FormGroup;
  categortLists: any;
  selectValue: any;
  viewData: any;
  constructor(private service: DataService, private activate: ActivatedRoute, private route: Router, private formBuilder: FormBuilder, private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.editForm = this.formBuilder.group({
      "subjectName": ['', Validators.compose([Validators.required, Validators.maxLength(60), Validators.minLength(2)])],
      "description": ['', Validators.compose([Validators.required, Validators.maxLength(256), Validators.minLength(2)])],
      "categoryname": ['', Validators.compose([Validators.required])]
    })
    this.activate.params.subscribe(params => {
      this.id = params.id
      console.log('id===', this.id)
    })
    this.viewmedicallist()
    this.categoryList()
  }


  categoryList() {
    console.log('dasdh')
    this.service.getApi('admin/getAllMedicalCategories', 1).subscribe(res => {
      console.log('resss==>>', res)
      this.categortLists = res.body.result
      console.log('categoryLists==>>', this.categortLists)
    })
  }

  viewmedicallist() {
    let object = {
      "medicalId": this.id,
      "subjectName": this.editForm.value.subjectName,
      "description": this.editForm.value.description
    }
    this.service.getApi('admin/viewMedicalCondition/' + this.id, 1).subscribe(res => {
      console.log("hhhhhhhhh=======>", res)
      this.viewData = res.body.result
      console.log('ress===>>', this.viewData)

      this.editForm.patchValue({
        "subjectName": this.viewData.subjectName,
        "description": this.viewData.description,
        "categoryname": this.viewData.categoryId
      })

    })

  }


  select(value) {
    this.selectValue = value
    console.log('selectValue', this.selectValue)
  }

  editMedicalList() {
    let object = {
      "medicalId": this.id,
      "subjectName": this.editForm.value.subjectName,
      "categoryId": this.selectValue,
      "description": this.editForm.value.description,
    }
    console.log('update==>>', object)
    this.service.postApi('admin/editMedicalCondition', object, 1).subscribe(res => {
      console.log("hhhhhhhhh=======>", res)
      if (res.body.response_code == 200) {
        this.spinner.show()
        this.store = res.body.result
        console.log('ress===>>', this.store)

        this.route.navigate(['/medical-condition-knowledge'])
      } else {
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
      }
    }, error => {
      this.spinner.hide()
      this.service.toastErr('Internal server error')
    })

  }
}
