import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-podcast-edit',
  templateUrl: './podcast-edit.component.html',
  styleUrls: ['./podcast-edit.component.css']
})
export class PodcastEditComponent implements OnInit {
  
  id: any;
  store: any;
  imageSrc:string = '';
  imageShow: any;
  editForm:FormGroup;


  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private formBuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {

    this.editForm=this.formBuilder.group({
      "categoryname"    : ["",Validators.required],
     
    })
    this.activate.params.subscribe(params=>{
      this.id=params.id
      console.log(this.id)
    })
    this.editCategory()
  }

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e) {
    let reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc)
    this.imageShow=this.imageSrc
  }

  editCategory(){
  
        this.service.getApi('admin/viewPodcastCategory/'+this.id,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
            this.store =res.body.result
            console.log('ress===>>',this.store)
          this.imageShow=this.store.image
          this.editForm.patchValue({
            "categoryname" : this.store.categoryName
          })
        
        })

    }
   
    
    editanimation(){
      this.spinner.show()
      let object ={
          "categoryId" : this.id,
          "categoryName" : this.editForm.value.categoryname,
          "image" : this.imageShow
      }
          this.service.postApi('admin/editPodcastCategory',object,1).subscribe(res=>{
              console.log("hhhhhhhhh=======>",res)
              if(res.body.response_code == 200 ){
                
              this.store =res.body.result
              this.imageShow=this.store.image
              this.spinner.hide()
              this.route.navigate(['/podcast-management-category'])
              }else{
                this.spinner.hide()
              }
          },error=>{
            this.service.toastErr('Internal server error')
            this.spinner.hide()
          })
  
      }

}
