import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-add-medical-condition-knowledge',
  templateUrl: './add-medical-condition-knowledge.component.html',
  styleUrls: ['./add-medical-condition-knowledge.component.css']
})
export class AddMedicalConditionKnowledgeComponent implements OnInit {
  categortLists: any;
  addMedicalList:FormGroup;
  descriptionForm:FormGroup;
 
  categoryId='';
    object: { "categoryId": string };


  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private formBuilder:FormBuilder) { }

      ngOnInit() {
      
      this.addMedicalList=this.formBuilder.group({
      "subjectName" :  ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
      })
      this.descriptionForm=this.formBuilder.group({
        "description" :  ['',Validators.compose([Validators.required,Validators.maxLength(256),Validators.minLength(10)])]
        })
      this.categoryList();
      }
      // /////////////Admin Animation Category List ///////////////
      categoryList(){
      // let object ={
      // "status"    : null
      // } 
      // console.log('status==>>',object)
      this.service.getApi('admin/getAllMedicalCategories',1).subscribe(res=>{
      console.log('resss==>>',res)
      this.categortLists=res.body.result
      console.log('categoryLists==>>',this.categortLists)
      })
      }

    
 
  
      //////////Evenet for get category id//////////////////////
  select(value){
      this.categoryId=value
      console.log('categoryName==>>',this.categoryId)
      }

      // //////////Add Medical api////////////////////
 addList(){
  
   let object={
    "categoryId"    : this.categoryId,
    "subjectName"   : this.addMedicalList.value.subjectName,
    "description"   : this.descriptionForm.value.description,
     
    }
    console.log('object',object)
    this.service.postApi('admin/addMedicalCondition',object,1).subscribe(res=>{
    console.log('resEdit===>>',res)

    if(res.body.response_code == 200 ){
    this.service.showSuccess(res.body.response_message)
    this.route.navigate(['medical-condition-knowledge'])
    }

    },error=>{
    this.service.toastErr('Internal server error')
    })

  

    }
}
