import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
declare  var $:any;
@Component({
  selector: 'app-video-management',
  templateUrl: './video-management.component.html',
  styleUrls: ['./video-management.component.css']
})
export class VideoManagementComponent implements OnInit {
  limit = 10;
  total: any;
  page = 1;
  p = 0;
  videodata: any=[];
  searchname: any;
  videoDeleteId: any;
  search: string;

  constructor(private service: DataService, private router: Router,private spinner:NgxSpinnerService) { }

  ngOnInit() {
   
    this.videolists()
  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1
    console.log('searchName==>>', this.searchname)
    this.videolists()
    }

  reset() {
    this.searchname = ''
    // this.searchForm.reset();
    this.search='';
    this.videolists()


  }

  videolists() {
    this.spinner.show()
    this.videodata=[];
    let object = {
      "search": this.searchname,
      "page": this.page,
      "limit": this.limit,
    }
    console.log('page===>>', object)
    this.service.postApi('admin/animationVideoList', object, 1).subscribe(res => {
      console.log('resdfdsfsdfdss==>>', res)
      if(res.body.response_code == 200 ){

      this.videodata = res.body.result.docs ? res.body.result.docs : res.body.result
      // this.limit=res.body.result.docs.limit

      this.total = res.body.result.total
      this.spinner.hide()
      }else{
        this.spinner.hide()
      }


    },error=>{
      this.service.toastErr('Internal server error')
    })
  }

  changePage(page) {
    console.log('Page ', page)
    this.page = page;
    this.p = page - 1;
    this.videolists()
  }

  deleteId(id){
    this.videoDeleteId=id
    $('#modalDelete').modal({ backdrop: 'static', keyboard: false })
    // $('#modalDelete').modal({backdrop: 'static', keyboard: false})
    console.log('videoDeleteId',this.videoDeleteId)
  }

  deleteFunctions(){ 


    let object={
    "videoId" : this.videoDeleteId
    }
    this.service.postApi('admin/deleteAnimationVideo',object,1).subscribe(res=>{
    console.log('res===>>>',res)
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this.videolists()
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }
}
