import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-subscription',
  templateUrl: './edit-subscription.component.html',
  styleUrls: ['./edit-subscription.component.css']
})
export class EditSubscriptionComponent implements OnInit {
  editForm:FormGroup;
  imageSrc: any;
  subscripid: any;
  subscripdata: any;
  constructor(private service:DataService,private formbuilder:FormBuilder,private spinner:NgxSpinnerService,private router:Router,private activate:ActivatedRoute) { }

  ngOnInit() {
    this.editForm=this.formbuilder.group({
      "subscripname"    : ["",Validators.compose([Validators.required])],
      "validityPeriod"  : ["",Validators.compose([Validators.required])],
      "cost"            : ["",Validators.compose([Validators.required])],

    })

    this.activate.params.subscribe(res=>{
      this.subscripid=res.id
        console.log('id==>>',this.subscripid)
    })
    this.viewsubscrip()
  }

  // handleInputChange(e) {
  //   var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  //   var pattern = /image-*/;
  //   var reader = new FileReader();
  //   if (!file.type.match(pattern)) {
  //     alert('invalid format');
  //     return;
  //   }
  //   reader.onload = this._handleReaderLoaded.bind(this);
  //   reader.readAsDataURL(file);
  // }
  // _handleReaderLoaded(e) {
  //   let reader = e.target;
  //   this.imageSrc = reader.result;
  //   console.log(this.imageSrc)
  // }

  viewsubscrip(){
    this.spinner.show()
   

    this.service.getApi('admin/viewSubscription/'+this.subscripid,1).subscribe(res=>{
      if(res.body.response_code == 200 ){
        this.spinner.hide()
          // this.service.showSuccess(res.body.response_message)
            this.subscripdata=res.body.result
              console.log('data==>>',this.subscripdata)
                // this.router.navigate(['subscription-management'])
                this.editForm.patchValue({
                  "subscripname"      :   this.subscripdata.subscriptionName,
                  "validityPeriod"    :   this.subscripdata.validityPeriod,
                  "cost"              :   this.subscripdata.cost
                })
      }
    },error=>{
      this.spinner.hide()
      this.service.toastErr('Internal server error')
    })
  }

  updatesubscrip(){
    this.spinner.show()
    let object={
      "subscriptionId"      : this.subscripid,
      "subscriptionName"    : this.editForm.value.subscripname,
      "validityPeriod"      : this.editForm.value.validityPeriod,
      "cost"                : this.editForm.value.cost
    }
    console.log('object==>>',object)
    this.service.postApi('admin/editSubscription',object,1).subscribe(res=>{
      if(res.body.response_code == 200 ){
        this.spinner.hide()
          this.service.showSuccess(res.body.response_message)
            this.router.navigate(['subscription-management'])
      }
    },error=>{
      this.service.toastErr('Internal server error')
    })
  }
}
