import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-staticedit',
  templateUrl: './staticedit.component.html',
  styleUrls: ['./staticedit.component.css']
})
export class StaticeditComponent implements OnInit {
  editorValue: any;
  viewdata: any;
  editForm:FormGroup;
  edit_type: any;
  constructor(private service:DataService,private router:Router,private active:ActivatedRoute,private fb:FormBuilder) { }

  ngOnInit() {
    // //////////THIS ID GET FROM STATIC PAGE MANAGEMENT ////////////
    this.active.params.subscribe(res=>{
      console.log('edit_type',res.id)
      this.edit_type=res.type
    })

    this.editForm=this.fb.group({
      "edit"    : ['',[Validators.required,]],
      "title"   : ['',[Validators.required,]],
    })
    this.staticvied()
  }
      // ///////////////////STATIC VIEW API ///////////////
 staticvied(){
  let object={
    "type"  : this.edit_type
  }
  this.service.postApi('static/viewStaticContent',object,1).subscribe(res=>{
   console.log('staticView==>>',res)
   if(res.body.response_code ==200){
     this.viewdata=res.body.result
     console.log('viewdata',this.viewdata)
     this.editForm.patchValue({
      "title" : this.viewdata.title, 
       "edit" : this.viewdata.description
     })
   }
  })
  }

  // /////////////EDIT STATIC CONTRNT API ////////////////
  editor(){
      
      let object={
      "staticId"      : this.viewdata._id,
      "title"         : this.editForm.value.title ,
      "description"    : this.editForm.value.edit
    }
    console.log('editorvalue',object)
    this.service.postApi('static/editStaticContent',object,1).subscribe(res=>{
      console.log('res====>>',res)
      if(res.body.response_code == 200){
        this.service.showSuccess(res.body.response_message)
        this.router.navigate(['static-management'])
      }else{
        this.service.toastErr(res.body.response_message)
      }
    })
    
  }

}
