import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.css']
})
export class ViewContactComponent implements OnInit {
  contact_id: any;
  contactView: any;

  constructor(private service:DataService,private activate:ActivatedRoute) { }

  ngOnInit() {
    this.activate.params.subscribe(res=>{
      this.contact_id=res.id
        console.log('contact_id===>>',this.contact_id)
    })
    this.viewContact()
  }

  viewContact(){ 
        this.service.getApi('admin/viewContactUs/'+this.contact_id,1).subscribe(res=>{
        
          this.contactView=res.body.result
          console.log("contactView=======>",this.contactView)
     
      })

    }

}
