import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {
  changePasswordForm:FormGroup;
  show2: boolean=false;
  show1: boolean = false;
  type :any= "password";

  show :boolean = false;
  type2: string="password";
  type1: string="password";
  constructor(private service:DataService,private router:Router,private formBuilder:FormBuilder) { }

  ngOnInit() {

    this.changePasswordForm=this.formBuilder.group({
      'oldPassword'     :   ['',Validators.required],
      'newPassword'     :   ['',Validators.compose([Validators.required,Validators.minLength(8),Validators.maxLength(16)])],
      'confirmPassword' :   ['',Validators.required]
    })
  }


  // ///////////////CHANGE PASSWORD API////////////////////
  changePassword(){
    let object={
      // "_id"               : this.id,
      "oldPassword"       : this.changePasswordForm.value.oldPassword,
      "newPassword"       : this.changePasswordForm.value.newPassword,
      "confirmPassword"   : this.changePasswordForm.value.confirmPassword 

    }
    // console.log('iddddd',this.id)
    // console.log('object',object)
    this.service.postApi('admin/changePassword',object,1).subscribe(res=>{
      console.log('reset===>>',res)
      if(res.body.response_code == 200){
        console.log('res==>>',res.body)
        this.service.showSuccess(res.body.response_message)
        this.router.navigate(['login'])
        this.changePasswordForm.reset()
      }else{
        this.service.toastErr(res.body.response_message)
      }
    

    },error=>{
      this.service.toastErr("Internal server error");
    })
  }


  toggleShow() {
    this.show = !this.show;
    if (this.show) {
      this.type = "text";
    }
    else {
      this.type = "password";
    }
  }

  toggleShow1() {
    
    this.show1 = !this.show1;
    if (this.show1) {
      this.type1 = "text";
    }
    else {
      this.type1 = "password";
    }
    }
    toggleShow2() {
      this.show2 = !this.show2;
      if (this.show2) {
        this.type2 = "text";
      }
      else {
        this.type2 = "password";
      }
    }

}
