import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;

@Component({
  selector: 'app-episode-management',
  templateUrl: './episode-management.component.html',
  styleUrls: ['./episode-management.component.css']
})
export class EpisodeManagementComponent implements OnInit {
  searchname:any;

  token: any;
  tokens: string;
  userdata: any;
  limit=10;
  total: any;
  page=1;
  p = 0;
  user_id: any;
  block_id: any;
  unblock_id: any;
  unblock_ids: any;
  Edit : any;
  episoddata: any = [];searchs: string;
;
  searchForm:FormGroup

  constructor(private service:DataService,private router:Router,private formBuilder:FormBuilder,private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.episodlists()
  }

   Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1
    console.log('searchName==>>', this.searchname)
    this.episodlists()
    }


  episodlists(){  
    this.spinner.show()
    this.episoddata=[];
    let object={
    "search" : this.searchname,
    "limit": this.limit,
    "page": this.page
   
    }
    console.log('page===>>',object)
    this.service.postApi('admin/animationEpisodeList',object,1).subscribe(res=>{
    if(res.body.response_code == 200){
      this.spinner.hide()
    this.episoddata=res.body.result.docs?res.body.result.docs:res.body.result
    console.log('episoddata',this.episoddata)
    this.total=res.body.result.total;
    console.log('total==>>',this.total)
    }else{
      this.spinner.hide()
    }
    },error=>{
      this.spinner.hide()
    })
    }

    changePage(page) {
      console.log('Page ', page)
      this.page = page;
      this.p = page-1;
      this.episodlists()   
      }



deleteFunction(id){ 

    this.user_id=id
    console.log('iddsfsdf==>>',this.user_id)
    $('#exampdelete').modal({ backdrop: 'static', keyboard: false })
    }

deleteFunctions(){ 
    let object={
    "episodeId" : this.user_id
    }
    this.service.postApi('admin/deleteAnimationEpisode',object,1).subscribe(res=>{
    console.log('res===>>>',res)
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this.episodlists()
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }

    reset(){
      this.searchs='';
      this.searchname='';
      this.episodlists()  
    }








}
