import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-static-management',
  templateUrl: './static-management.component.html',
  styleUrls: ['./static-management.component.css']
})
export class StaticManagementComponent implements OnInit {
  editorValue: any;
  staticuserdata: any;

  constructor(private service: DataService, private router: Router, private spinner: NgxSpinnerService) { }

  ngOnInit() {
  this.staticpagedetail();
  }

  editor() {
              console.log('editorvalue', this.editorValue)
            }


  staticpagedetail() {
    this.spinner.show()
      console.log('asdasdahsgdjasdas')
        this.service.getApi('static/staticContentList', 1).subscribe(res => {
          if (res.body.response_code == 200) {
            this.spinner.hide()
          this.staticuserdata = res.body.result
        console.log('staticuserdata====>>>', this.staticuserdata)
      }
    }, error => {
  this.spinner.hide()
})
}
}
