import { Component, OnInit, HostListener } from '@angular/core';
import { DataService } from '../data.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-subscription',
  templateUrl: './add-subscription.component.html',
  styleUrls: ['./add-subscription.component.css']
})
export class AddSubscriptionComponent implements OnInit {
  @HostListener('window:keydown', ['$event'])
  editForm:FormGroup;
  imageSrc: any;
  constructor(private service:DataService,private formbuilder:FormBuilder,private spinner:NgxSpinnerService,private router:Router) { }

  ngOnInit() {
    this.editForm=this.formbuilder.group({
      "subscripname"    : ["",Validators.compose([Validators.required])],
      "validityPeriod"  : ["",Validators.compose([Validators.required])],
      "cost"            : ["",Validators.compose([Validators.required])],

    })

    
  }
  

  // handleInputChange(e) {
  //   var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  //   var pattern = /image-*/;
  //   var reader = new FileReader();
  //   if (!file.type.match(pattern)) {
  //     alert('invalid format');
  //     return;
  //   }
  //   reader.onload = this._handleReaderLoaded.bind(this);
  //   reader.readAsDataURL(file);
  // }
  // _handleReaderLoaded(e) {
  //   let reader = e.target;
  //   this.imageSrc = reader.result;
  //   console.log(this.imageSrc)
  // }

  addsubscrip(){
    this.spinner.show()
    let object={
      "subscriptionName"    : this.editForm.value.subscripname,
      "validityPeriod"      : this.editForm.value.validityPeriod,
      "cost"                : this.editForm.value.cost
    }
    console.log('valid==>>',this.editForm.valid)
    this.service.postApi('admin/addSubscription',object,1).subscribe(res=>{
      if(res.body.response_code == 200 ){
        this.spinner.hide()
          this.service.showSuccess(res.body.response_message)
            this.router.navigate(['subscription-management'])
      }else if(res.body.response_code == 209){
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
      }else{
        this.spinner.hide()
        this.service.toastErr(res.body.response_message)
      }
      

    },error=>{
      this.spinner.hide()
      this.service.toastErr('Internal server error')
    })
  }

}
