import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Profile } from 'selenium-webdriver/firefox';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.css']
})
export class MyProfileComponent implements OnInit {
  myProfileForm:FormGroup;
  private imageSrc: string = '';
  profile: any;
  data: any;
  constructor(private Service:DataService,private router:Router,private FormBuilder:FormBuilder) { }

  ngOnInit() {
    this.myProfileForm=this.FormBuilder.group({
      // "profileImage"    :   [''],
      "name"            :   ['',Validators.compose([Validators.required,Validators.maxLength(255),Validators.minLength(2),Validators.pattern(/^([a-z']+(-| )?)+$/i)])],
      "buddies"         :   ['',Validators.required]
    })
    
    this.myProfile()
  }

 

  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.profile = reader.result;
    console.log(this.profile)
  }

  /////////only take number validate////////////////
  toCheckSpaceChar(event) {
      if (event.charCode>47 && event.charCode<58) 
      {
        return true;
      } else{
        return false;
    }
  }
  

  // ////////////////GET PROFILE API //////////////////////
  myProfile(){
   this.Service.getApi('admin/getProfile',1).subscribe(res=>{
    if(res.status == 200){
      // this.Service.showSuccess('Details have been fetched successfully')
      this.profile=res.body.result.profilePic
      this.data=res.body.result
      console.log('name',this.data)
      this.myProfileForm.patchValue({
        "name"        :    this.data.firstName,
        "buddies"     :    this.data.maxBuddies
      })
    }
   },error=>{
     this.Service.toastErr('Internal server error')
   })
    
  }

  editProfile(){
    let object={
        "profilePic"  : this.profile,
        "firstName"   : this.myProfileForm.value.name,
        "maxBuddies"  : this.myProfileForm.value.buddies,

    }
    // console.log('value',this.myProfileForm.value.buddies)
    console.log('object',object)
    this.Service.postApi('admin/editProfile',object,1).subscribe(res=>{
      console.log('resEdit===>>',res)
      if(res.body.response_code == 200 ){
      this.profile=res.body.result.profilePic
      this.Service.showSuccess(res.body.response_message)
      this.router.navigate(['dashboard'])
      console.log('profile===>>',this.profile)
      this.myProfile();
      } else
      this.Service.toastErr(res.body.response_message) 

    },error=>{
      this.Service.toastErr("Internal server error")
    })
  }

}
