import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user-view-company',
  templateUrl: './user-view-company.component.html',
  styleUrls: ['./user-view-company.component.css']
})
export class UserViewCompanyComponent implements OnInit {
  user_id: any;
  page=1;
  limit=1;
  viewdata: any;
  p = 0;
  member: any;
  total: any;

  constructor(private service:DataService,private router:Router,private activate:ActivatedRoute) { }

  ngOnInit() {
     // /////////////USER ID GET FROM USER MANAGEMENT COMPONENT/////////////
  this.activate.params.subscribe(res=>{
    this.user_id=res.id
      console.log('user_id===>>',this.user_id)
  })
  this.viewdetail();
}

 // //////////////////////VIEW COMPANY API///////////////////////

 viewdetail(){
  let object={
      "userId"  : this.user_id,
      "page": this.page,
      "limit": this.limit,
  }
  this.service.postApi('admin/viewCompany',object,1).subscribe(res=>{
  console.log('res==>>',res) 
  if(res.body.response_code == 200){
  this.viewdata=res.body.result
  console.log('vuewdata==>>',this.viewdata)
  
  this.service.showSuccess(res.body.response_message)     
  }else{
  this.service.toastErr(res.body.response_message)     
  }  
  })
  }
 

  changePage(page) {
    console.log('Page ', page)
    this.page = page;
    this.p = page-1;
    this.viewdetail()   
    }

}



