import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-add-mental-health-campaign-category',
  templateUrl: './add-mental-health-campaign-category.component.html',
  styleUrls: ['./add-mental-health-campaign-category.component.css']
})
export class AddMentalHealthCampaignCategoryComponent implements OnInit {

  imageUrl: string;
  tokenId: any;
  type: any;
  base64Image: any;
  private imageSrc: string = '';
 enteredName: '';
  CategoryName: any;
  addForm: FormGroup;
  base64textString: string;
  object: { "categoryName": any; "image": string; };

  constructor(private Service:DataService,private router:Router,private fb:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
      this.addForm=this.fb.group({
      names      : ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
      })

      }

  // ////////Event for image upload  in 64 base//////////////////////
  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.imageSrc = reader.result;
    console.log(this.imageSrc)
  }
      // FDSFHGFDSHFSDFHDFSHJDSFDSFGDSHFDSFHDSFSDGDFDSFHFHDFGSJHDFS
  addAnimationCategory(){
    
    if(this.imageSrc!=""){
      this.spinner.show()
        this.object={
          "categoryName"   : this.addForm.value.names,
          "image"  : this.imageSrc,
           
        }

  

    this.Service.postApi('admin/addMentalHealthCategory',this.object,1).subscribe(res=>{
      console.log('resEdit===>>',res)
      if(res.body.response_code == 200 ){
        this.spinner.hide()
        this.router.navigate(['mental-health-campaign-category'])
      }else{
        this.spinner.hide()
        this.Service.toastErr(res.body.response_message)
      }
    },error=>{
      this.spinner.hide()
      this.Service.toastErr('Internal server error')
    })
   
  }else{
    this.Service.toastErr('Please upload podcast category image')
  }
    // console.log('object',object)
    
  }

}
