import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-add-audio-management',
  templateUrl: './add-audio-management.component.html',
  styleUrls: ['./add-audio-management.component.css']
})
export class AddAudioManagementComponent implements OnInit {
  categortLists: any;
  addEpidodeForm:FormGroup;
  categoryId='';
  episodeId: any;
  audioSrc: any;
  safeSrc: any;
  episodeLists: any;
  audioImageSrc: any;


  constructor(private service:DataService,private activate:ActivatedRoute,private router:Router,private formBuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.addEpidodeForm=this.formBuilder.group({
      "audioName" :  ['',Validators.compose([Validators.required,Validators.maxLength(60),Validators.minLength(2)])]
    })
    this.episodeList()
    this.categoryList();

  }


  // /api/v1/admin/getAllAnimationEpisodes


  episodeList(){
    this.service.getApi('admin/getAllPodcastEpisodes',1).subscribe(res=>{
      this.episodeLists=res.body.result
    })
  }


  categoryList(){
    this.service.getApi('admin/getAllPodcastCategories',1).subscribe(res=>{
      this.categortLists=res.body.result
    })
  }



  handleInputChange(e) {
    var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    var pattern = /image-*/;
    var reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }
  _handleReaderLoaded(e) {
    let reader = e.target;
    this.audioImageSrc = reader.result
    // this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.audioSrc);
    // console.log('audioSrc',this.safeSrc)
  
}


handleInputChanges(e) {
  var file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
  var pattern = /audio/;
  var reader = new FileReader();
  if (!file.type.match(pattern)) {
    alert('invalid format');
    return;
  }
  reader.onload = this._handleReaderLoadeds.bind(this);
  reader.readAsDataURL(file);
}
_handleReaderLoadeds(e) {
  let reader = e.target;
  this.audioSrc = reader.result
  // this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.audioSrc);
  // console.log('audioSrc',this.safeSrc)

}


 select(value){
  this.categoryId=value
 }
 selects(value){
  this.episodeId=value
 }
 

 addaudio(){
   this.spinner.show()
  // alert("jkdfhejkqwhfewfjklhewrfyer")
  let object={
    "categoryId"    : this.categoryId,
    "episodeId"     : this.episodeId,
    "audioName"     : this.addEpidodeForm.value.audioName,
    "audio"         : this.audioSrc,
    "thumbnail"     : this.audioImageSrc
     
  }
  this.service.postApi('admin/addPodcastAudio',object,1).subscribe(res=>{
    if(res.body.response_code == 200 ){
      this.spinner.hide()
      this.service.showSuccess(res.body.response_message)
      this.audioSrc=res.body.result.docs
      
      // this.safeSrc =  this.sanitizer.bypassSecurityTrustResourceUrl(this.audioSrc);
      this.router.navigate(['audio-management'])
    }else{
      this.spinner.hide()
      this.service.toastErr("Please upload category audio")
    }


  },error=>{
    this.spinner.hide()
    this.service.toastErr('Internal server error')
  })
  // this.router.navigate(['/animation-management-category'])
}
}
