import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { DataService } from '../data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-view-animation',
  templateUrl: './view-animation.component.html',
  styleUrls: ['./view-animation.component.css']
})
export class ViewAnimationComponent implements OnInit {
  editForm:FormGroup;
  id: any;
  store: any;
  imageShow: any;
  constructor(private service:DataService,private activate:ActivatedRoute,private route:Router,private formBuilder:FormBuilder,private spinner:NgxSpinnerService) { }

  ngOnInit() {

  
    this.activate.params.subscribe(params=>{
      this.id=params.id
      console.log(this.id)
    })
    this.viewCategory()
  }

  viewCategory(){
    this.spinner.show()
    let object ={
        "categoryId" : this.id
    }
        this.service.postApi('admin/viewAnimationCategory',object,1).subscribe(res=>{
            console.log("hhhhhhhhh=======>",res)
            if(res.body.response_code == 200 ){
              this.spinner.hide()
            this.store =res.body.result
            console.log('ress===>>',this.store)
          this.imageShow=this.store.image
            }
         
        
        },error=>{
          this.spinner.hide()
        })

    }

}
