import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-medical-condition-knowledge',
  templateUrl: './medical-condition-knowledge.component.html',
  styleUrls: ['./medical-condition-knowledge.component.css']
})
export class MedicalConditionKnowledgeComponent implements OnInit {

  searchname:any;
  token: any;
  tokens: string;
  userdata: any;
  limit=10;
  total: any;
  page=1;
  p = 0;
  user_id: any;
  block_id: any;
  unblock_id: any;
  unblock_ids: any;
  Edit : any;
  medicaldata: any = [];
  searchs: string;
  searchForm:FormGroup

  constructor(private service:DataService,private router:Router,private formBuilder:FormBuilder,private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.medicallists()
  }

   Searchvaluechange(searchdata){
    this.searchname=searchdata
    console.log('searchName==>>', this.searchname)
    this.medicallists()
    }


  medicallists(){  
    this.spinner.show()
    this.medicaldata=[];
    let object={
    "search" : this.searchname,
    "limit": this.limit,
    "page": this.page
   
    }
    console.log('page===>>',object)
    this.service.postApi('admin/medicalConditionList',object,1).subscribe(res=>{
    if(res.body.response_code == 200){
      this.spinner.hide()
    this.medicaldata=res.body.result.docs?res.body.result.docs:res.body.result
    console.log('.medicaldata',this.medicaldata)
    this.total=res.body.result.total;
    console.log('total==>>',this.total)
    }else{
      this.spinner.hide()
    }
    },error=>{
      this.spinner.hide()
    })
    }
  medicalddata(arg0: string, medicalddata: any) {
    throw new Error("Method not implemented.");
  }

    changePage(page) {
      console.log('Page ', page)
      this.page = page;
      this.p = page-1;
      this.medicallists()   
      }



deleteFunction(id){ 

    this.user_id=id

    console.log('iddsfsdf==>>',this.user_id)
    $('#exampdelete').modal({ backdrop: 'static', keyboard: false })
    }

deleteFunctions(){ 
    let object={
    "medicalId" : this.user_id
    }
   
    this.service.postApi('admin/deleteMedicalCondition',object,1).subscribe(res=>{
    console.log('res===>>>',res)
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this.medicallists()
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }

    reset(){
      this.searchs='';
      this.searchname='';
      this.medicallists()  
    }



}
