import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
declare var $:any;
@Component({
  selector: 'app-audio-management',
  templateUrl: './audio-management.component.html',
  styleUrls: ['./audio-management.component.css']
})
export class AudioManagementComponent implements OnInit {
  limit = 10;
  total: any;
  page = 1;
  p = 0;
  // audiodata : any=[];
  searchname: any;
  
  search: string;
  audiodata: any=[];
  audioDeleteId: any=[];

  constructor(private service: DataService, private router: Router,private spiner:NgxSpinnerService) { }

  ngOnInit() {
   
    this.audiolists() 
  }

  Searchvaluechange(searchdata){
    this.searchname=searchdata
    this.page=1
    console.log('searchName==>>', this.searchname)
    this.audiolists() 
    }

  reset() {
    this.searchname = ''
    // this.searchForm.reset();
    this.search='';
    this.audiolists() 


  }

  audiolists() {
    this.spiner.show()
    this.audiodata =[];
    let object = {
      "search": this.searchname,
      "page": this.page,
      "limit": this.limit,
    }
    console.log('page===>>', object)
    this.service.postApi('admin/podcastAudioList', object, 1).subscribe(res => {
      if(res.body.response_code == 200 ){
      this.spiner.hide()
      this.audiodata = res.body.result.docs ? res.body.result.docs : res.body.result
      console.log('audiodata ===>', this.audiodata )
      // this.limit=res.body.result.docs.limit
      this.total = res.body.result.total
      console.log('audiodata ==>>', this.total, this.audiodata )
      }else{
        this.spiner.hide()

      }


    },error=>{
      this.service.toastErr('Internal server error')
    })
  }

  changePage(page) {
    console.log('Page ', page)
    this.page = page;
    this.p = page - 1;
    this.audiolists() 
  }

  deleteId(id){
    this.audioDeleteId=id
    $('#modalDelete').modal({ backdrop: 'static', keyboard: false })
    // $('#modalDelete').modal({backdrop: 'static', keyboard: false})
    console.log('audioDeleteId',this.audioDeleteId)
  }

  deleteFunctions(){ 


    let object={
    "audioId" : this.audioDeleteId
    }
    this.service.postApi('admin/deletePodcastAudio',object,1).subscribe(res=>{
    console.log('res===>>>',res)
    if(res.body.response_code == 200){
    this.service.showSuccess(res.body.response_message)
    this.audiolists() 
    }else{
    this.service.toastErr(res.body.response_message)
    }
    })
    }

}
